body{
  background-color:#eef6f8;
}
.App{
  display: flex;
  flex-direction: column;
}
.navLinks{
  position: absolute;
  display: flex;
  width: 99%;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  z-index: 1;
  font-size: 1rem;
}
h5{
  margin: 2%;
  color:#36362e;
}
.about-me-section{
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 5%;
  margin-bottom: 5%;
  display: flex;
  flex-direction: column;

}
.icon{
  z-index: 1;
  position: absolute;
  top: 30%;
  left: 3%;
  width: 15%;
  align-self: flex-start;
  max-width: 200px;
  height: auto;
}
.AboutMeIcon{
  width: 35%;
  align-self: center;
}
.title{
  color: #ffffff;
  font-family: 'Dancing Script', cursive;
  position: relative;
  font-size: 3rem;
  text-align: center;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.596);
  margin: 1%;
  border: 3px solid #ffffff;
  background-color: rgba(0, 0, 0, 0.253);
}
.Banner{
  display: flex;
  justify-content: center;
  align-items: center;
}