@media(max-width: 375px){
    .piece{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 10%;
        margin-right: 10%;
    }
    .project-image{
        width: 75%;
        height:auto;
        max-height: 300px;
        margin-top: 15%;
        border-radius: 5px;
    }
    .my-work-body{
        text-align: center;
    }
    .project-title{
        font-size: 2rem;
        text-align: center;
    }
    .links{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .link{
        text-decoration: none;
        margin: 2%; 
        margin-bottom: 10%;
        color: white;
        width: 45%;
        line-height: 175%;
        text-align: center;  
        border-radius: 25px;
        background-color: #3698e2;
    }
}

